function loaders() {
    const game = document.body.dataset["game"];
    console.log("Body data game is '" + game + "'");
    if (game)
        import("./" + game + "/backgroundgen.ts").then(m => m.default());
    const scripts = document.getElementById("script-list");
    if (scripts) {
        const set = scripts.innerHTML.split(",");
        for (var i = 0; i < set.length; i++) {
            set[i] = set[i].replace("\r", "").replace("\n", "").trim();
            console.log("Importing local script '" + set[i] + "'");
            import("./" + set[i] + "_rawscript.ts").then(m => m.default());
        }
    }
}
if (document.readyState === "loading")
    document.addEventListener('DOMContentLoaded', loaders);
else
    loaders();
export {};
